import axios from "axios";

const url = "/api";

export default class API{

    //GET ALL POSTS
    static async allPosts()
    {
        console.log("getall")
        const res = await axios.get(`/posts/all`);
        console.log(res.data)
        return res.data;
    }

    //SEE POST
    static async seePost(post_id)
    {
        console.log("seePost")
        const res = await axios.get(`$/posts/see/${post_id}`);
        console.log(res.data)
        return res.data;
    }

    //GET USER POSTS
    static async userPosts(username)
    {    console.log("user_posts")
        const res = await axios.get(`${url}/posts/${username}`);
        console.log(res.data)
        return res.data;
    }

    //CREATE POST
    static async createPost(post)
    {   
        console.log("create")
        console.log(post)
        const res = await axios.post(`${url}/posts/create`,post);
        console.log(res.data)
        return res.data;
    }

    //UPDATE POST
    static async updatePost(post_id,post)
    {   
        console.log("update")
        const res = await axios.patch(`${url}/posts/update/${post_id}`,post);
        console.log(res.data)
        return res.data;
    }

    //DELETE POST
    static async deletePost(post_id)
    {
        console.log("delete")
        const res = await axios.delete(`${url}/posts/delete/${post_id}`);
        console.log(res.data)
        return res.data;
    }

}